import React, { useState } from "react";
import axios from "axios";
import {Link, useNavigate} from "react-router-dom";
import {
    MDBBtn,
    MDBContainer,
    MDBRow,
    MDBCol,
    MDBCard,
    MDBCardBody,
    MDBInput,
    MDBCheckbox,
    MDBIcon
}
    from 'mdb-react-ui-kit';
import {Button} from "react-bootstrap"
function Register() {
    const navigate = useNavigate();

    const [fullName, setFullName] = useState("");
    const [address, setAddress] = useState("");
    const [phoneNumber, setPhoneNumber] = useState("");
    const [username, setUsername] = useState("");
    const [password, setPassword] = useState("");
    const [confirmPassword, setConfirmPassword] = useState("");
    const [token, setToken] = useState("");
    const [passwordError, setPasswordError] = useState("");
    const [confirmPasswordError, setConfirmPasswordError] = useState("");
    const [fullNameError, setFullNameError] = useState("");
    const [addressError, setAddressError] = useState("");
    const [phoneNumberError, setPhoneNumberError] = useState("");
    const [usernameError, setUsernameError] = useState("");


    const handleFullNameChange = (event) => {
        const value = event.target.value;
        setFullName(value);
        if (!value) {
            setFullNameError("Vui lng nhp tn ca bn");
        } else {
            setFullNameError("");
        }
    };

    const handleAddressChange = (event) => {
        const value = event.target.value;
        setAddress(value);
        if (!value) {
            setAddressError("Vui lng nhp a ch");
        } else {
            setAddressError("");
        }
    };

    const handlePhoneChange = (event) => {
        const value = event.target.value;
        setPhoneNumber(value);
        if (!value) {
            setPhoneNumberError("Vui lng nhp s in thoi");
        } else {
            setPhoneNumberError("");
        }
    };

    const handleUsernameChange = (event) => {
        const value = event.target.value;
        setUsername(value);
        if (!value) {
            setUsernameError("Vui lng nhp tn ti khon");
        } else {
            setUsernameError("");
        }
    };


    const handlePasswordChange = (event) => {
        const password = event.target.value;
        const strongRegex = new RegExp("^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#$%^&*])(?=.{8,})");
        if (strongRegex.test(password)) {
            setPassword(password);
            setPasswordError("");
        } else {
            setPassword(password);
            setPasswordError("Mt khu phi bao gm t nht 1 ch in hoa, 1 ch s, 1 k t c bit!");
        }
    };

    const handleConfirmPasswordChange = (event) => {
        const confirmPassword = event.target.value;
        if (confirmPassword === password) {
            setConfirmPassword(confirmPassword);
            setConfirmPasswordError("");
        } else {
            setConfirmPassword(confirmPassword);
            setConfirmPasswordError("Mt khu nhp li khng khp!");
        }
    };

    const handleRegister = () => {
        if (passwordError || confirmPasswordError || fullNameError || addressError || phoneNumberError || usernameError) {
            alert("Vui lng in y  thng tin v sa cc li trc khi ng k!");
            return;
        }

        axios
            .post("http://fall2324w3g8.int3306.freeddns.org/api/auth/register", { username, password, address, phoneNumber, fullName })
            .then((response) => {
                const token = response.data.token;
                localStorage.setItem("token", token);
                setToken(token);
                alert("ng k ti khon thnh cng");
                navigate("/login");
            })
            .catch((error) => {
                console.error("ng k ti khon khng thnh cng", error);
                alert("Cc thng tin nhp khng hp l !");
            });
    };
    return (
        <MDBContainer fluid className='p-4 background-radial-gradient overflow-hidden'>

            <MDBRow>

                <MDBCol md='6' className='text-center text-md-start d-flex flex-column justify-content-center'>

                    <h1 className="my-5 display-3 fw-bold ls-tight px-3" style={{color: 'hsl(218, 81%, 95%)'}}>
                        BookShop <br />
                        <span style={{color: 'hsl(218, 81%, 75%)'}}>ng k thnh vin  </span>
                    </h1>

                    <p className='px-3' style={{color: 'hsl(218, 81%, 85%)'}}>
                        n vi ca hng ca chng ti, bn khng ch n
                        vi ni bn nhng cun sch cht lng v uy tn,
                        hy cng cm nhn v ho mnh vo l tng ca tc gi
                        trong nhng cun sch ni ting, hay v tm c nht ca ca hng
                        chng ti.
                    </p>

                </MDBCol>

                <MDBCol md='6' className='position-relative'>

                    <div id="radius-shape-1" className="position-absolute rounded-circle shadow-5-strong"></div>
                    <div id="radius-shape-2" className="position-absolute shadow-5-strong"></div>

                    <MDBCard className='my-5 bg-glass'>
                        <MDBCardBody className='p-5'>

                            <MDBInput wrapperClass='mb-4' id='fullNameInput' type='text' placeholder="Tn ca bn" value={fullName} onChange={handleFullNameChange}/>
                            {fullNameError && <div style={{ color: 'red' }}>{fullNameError}</div>}

                            <MDBInput wrapperClass='mb-4' id='addressInput' type='text' placeholder="a ch" value={address} onChange={handleAddressChange}/>
                            {addressError && <div style={{ color: 'red' }}>{addressError}</div>}

                            <MDBInput wrapperClass='mb-4' id='phoneInput' type='number' placeholder="S in thoi" value={phoneNumber} onChange={handlePhoneChange}/>
                            {phoneNumberError && <div style={{ color: 'red' }}>{phoneNumberError}</div>}

                            <MDBInput wrapperClass='mb-4' id='usernameInput' type='text' placeholder="Tn ti khon" value={username} onChange={handleUsernameChange}/>
                            {usernameError && <div style={{ color: 'red' }}>{usernameError}</div>}
                            <MDBInput wrapperClass='mb-4' id='passwordInput' type='password' placeholder="Mt khu" value={password} onChange={handlePasswordChange}/>
                            {passwordError && <div style={{ color: 'red' }}>{passwordError}</div>}

                            <MDBInput wrapperClass='mb-4' id='confirmPasswordInput' type='password' placeholder="Nhp li mt khu" value={confirmPassword} onChange={handleConfirmPasswordChange}/>
                            {confirmPasswordError && <div style={{ color: 'red' }}>{confirmPasswordError}</div>}

                            <Button className='w-100 mb-4' size='md' onClick={handleRegister}>ng k</Button>

                            <div className="text-center">
                                <p>Bn  c ti khon?</p>
                                <Link to="/login">ng nhp</Link>
                            </div>

                        </MDBCardBody>
                    </MDBCard>

                </MDBCol>

            </MDBRow>

        </MDBContainer>
    );
}

export default Register;