import React, { useEffect, useState } from "react";
import {Container, Row, Col, Card, Button, Tab, Tabs, Modal} from "react-bootstrap";
import axios from "axios";
import "./OrderManagement.css";
import "../AppRoutes";
import { getOrder, cancelOrder } from "../../apiServices/OrderManagementService";
import 'react-toastify/dist/ReactToastify.css';
import {ToastContainer,toast} from 'react-toastify'
import { Link } from 'react-router-dom';

const ConfirmationModal = ({ show, handleClose, handleConfirm }) => {
  return (
      <Modal show={show} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>Xc nhn hu n hng</Modal.Title>
        </Modal.Header>
        <Modal.Body>Bn c chc chn mun hu n hng ny?</Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            ng
          </Button>
          <Button variant="danger" onClick={handleConfirm}>
            Xc nhn
          </Button>
        </Modal.Footer>
      </Modal>
  );
};

const OrderManagement = () => {
  const storedAccessToken = localStorage.getItem('accessToken');
  const [orders, setOrders] = useState([]);

  const [orderStatuses, setOrderStatuses] = useState([
    "PENDING",
    "ACCEPTED",
    "DELIVERING",
    "SUCCESS",
    "RETURNED",
    "CANCELLED"
  ]);
  const translateStatus = (status) => {
    switch (status) {
      case "PENDING":
        return "Ch xc nhn";
      case "ACCEPTED":
        return "Ch ly hng";
      case "DELIVERING":
        return "ang giao";
      case "SUCCESS":
        return " giao";
      case "RETURNED":
        return "Tr hng";
      case "CANCELLED":
        return " hu";
      default:
        return status;
    }
  };
  const getOrderDetail = () => {
    getOrder().then(response => {
      setOrders(response.data);
    }).catch(error => {
      console.log(error)
    })
  }

  const actioncCancelOrder = (orderId) => {

  }

  useEffect(() => {
    getOrderDetail();
  }, []);

  return (
      <Tabs
          id="orderTabs"
          className="mb-3 order-tabs"
          fill
          defaultActiveKey={orderStatuses[0]} // Set the default active tab
      >
        {orderStatuses.map((status) => (
            <Tab key={status} eventKey={status} title={translateStatus(status)}>
              {/* Render orders based on the current status */}
              <OrderList status={status} orders={orders} />
            </Tab>
        ))}
      </Tabs>
  );
};

const OrderList = ({ status, orders }) => {
  // Ensure that orders is an array before using filter
  const [showModal, setShowModal] = useState(false);
  const [orderIdToCancel, setOrderIdToCancel] = useState(null);
  const filteredOrders = orders && Array.isArray(orders.orderSummaries)
      ? orders.orderSummaries.filter((order) => order.status === status)
      : [];
  const refresh = () => window.location.reload(true)
  const handleCancelClick = (orderId) => {
    setOrderIdToCancel(orderId);
    setShowModal(true);
  };

  const handleConfirmCancel = () => {
    if (orderIdToCancel) {
      // Perform cancellation logic here
      cancelOrder(orderIdToCancel)
          .then((response) => {
            toast.success("Hu n thnh cng");
          })
          .catch((error) => {
            console.log(error);
          })
          .finally(() => {

            refresh();
            setShowModal(false);

          });
    }
  };

  return (

      <div className="container-full-height order-tabs">
        {filteredOrders.map((order) => (

          <Card key={order.orderId} className="mb-5">
              <Card.Header>
                <Card.Text>
                  <Row>
                    <Col sm={4}></Col>
                    <Col sm={4}>
                      <Card.Text>M n hng: #{order.orderId}</Card.Text>
                    </Col>
                    <Col sm={4} style={{ textAlign: "right" }}>
                      <Card.Text className={`fw-bold cover-text-${order.status.toLowerCase()} ${order.status.toLowerCase()}`}>Trng thi: {order.status}</Card.Text>
                    </Col>
                  </Row>
                </Card.Text>
              </Card.Header>
              <Card.Body>
                {order.bookQuantitySummaries.map((bookSummary) => (
                    <Row key={bookSummary.bookSummary.bookId}>
                      <Col sm={4}>
                        <Card.Img
                            src={bookSummary.bookSummary.imgUrl.replace('public/', '')}
                            className="img-fluid w-25 mx-auto"
                        />
                      </Col>
                      <Col sm={4} className="d-flex align-items-center justify-content-center">
                        <Card.Title>Sch: {bookSummary.bookSummary.title}</Card.Title>
                      </Col>
                      <Col sm={1} className="d-flex align-items-center justify-content-center">
                        <p className="text-muted">x{bookSummary.quantity}</p>
                      </Col>
                      <Col sm={3} className="d-flex align-items-center justify-content-center">
                        <Card.Text>Thnh tin: {bookSummary.bookSummary.price * bookSummary.quantity}vn</Card.Text>
                      </Col>
                      <hr />

                    </Row>

                ))}
              </Card.Body>
              <Card.Footer>
                <Row>
                  <Col sm={3}></Col>
                  <Col className="text-muted" sm={6}>
                    Ngy t hng: {new Date(order.orderDate).toLocaleString()}
                  </Col>
                  <Col sm={3} className="fw-bold">
                    <Card.Text>Tng: {order.totalAmount}vn</Card.Text>
                  </Col>
                </Row>
              </Card.Footer>
            <Card.Footer>
              <Row>
                <Col sm={4}>
                  {/*<Button*/}
                  {/*    className="mb-2 mr-10"*/}
                  {/*    variant="success"*/}
                  {/*    disabled={status !== "PENDING"}*/}
                  {/*>*/}
                  {/*  Mua li*/}
                  {/*</Button>*/}
                </Col>
                <Col sm={4}>
                  {status === "SUCCESS" && (
                      <Link to={`/rate?orderId=${order.orderId}`} className="btn btn-secondary mb-2">
                        Xem nh gi
                      </Link>
                  )}
                </Col>
                <Col sm={4}>
                  <Button
                      className="mb-2"
                      variant="danger"
                      disabled={status !== "PENDING"}
                      onClick={() => handleCancelClick(order.orderId)}
                  >
                    Hu n
                  </Button>
                </Col>
              </Row>
            </Card.Footer>
            </Card>


        ))}
        <ToastContainer />
        <ConfirmationModal
            show={showModal}
            handleClose={() => setShowModal(false)}
            handleConfirm={handleConfirmCancel}
        />
      </div>
  );
};


export default OrderManagement;
