import React, { useEffect, useState } from 'react';
import './style.css';
import { Link } from "react-router-dom";
import axios from "axios";
import Box from '@mui/material/Box';
import Slider from '@mui/material/Slider';
import Typography from '@mui/material/Typography';

const SearchBar = ({ onSearch }) => {
    const [searchText, setSearchText] = useState('');

    const handleSearchChange = (e) => {
        const text = e.target.value;
        setSearchText(text);
        onSearch(text);
    };

    return (
        <div className="search-bar">
            <input
                type="text"
                placeholder="Search products..."
                value={searchText}
                onChange={handleSearchChange}
            />
            <button>Search</button>
        </div>
    );
};

const Product = ({ title, price, imgUrl, bookId }) => {
    const imagePath = imgUrl.replace('/public', '');

    return (
        <div className="product">
            <img src={imagePath} alt={title} />
            <h3>{title}</h3>
            <p>{price}</p>
            <Link to={`/shop/${bookId}`} className="view-more-button">Xem thm</Link>
        </div>
    );
};


const ProductGrid = ({ products, searchKeyword }) => {
    const filteredProducts = products.filter((product) =>
        product.title.toLowerCase().includes(searchKeyword.toLowerCase())
    );

    return (
        <div className="product-grid">
            {filteredProducts.map((product) => (
                <Product key={product.bookId} {...product} />
            ))}
        </div>
    );
};


const SortOptions = ({ onSort }) => {
    const [showOptions, setShowOptions] = useState(false);

    const toggleOptions = () => {
        setShowOptions(!showOptions);
    };

    const handleSort = (option) => {
        setShowOptions(false);
        onSort(option);
    };

    return (
        <div className="sort-options-container">
            <button className="sort-button" onClick={toggleOptions}>
                Sp xp cc cun sch
            </button>
            {showOptions && (
                <ul className="sort-options-list">
                    <li onClick={() => handleSort('price_asc')}>Sp xp gi tng dn</li>
                    <li onClick={() => handleSort('price_desc')}>Sp xp gi gim dn</li>
                    <li onClick={() => handleSort('rating')}>Sp xp theo nh gi</li>
                    <li onClick={() => handleSort('year')}>Sp xp theo nm sn sut</li>
                </ul>
            )}
        </div>
    );
};

const PriceFilter = ({ priceRange, onPriceChange }) => {
    const handleSliderChange = (event, newValue) => {
        if (newValue[0] <= newValue[1]) {
            onPriceChange(newValue);
        }
    };

    return (
        <Box sx={{ width: 300 }}>
            <Typography
                id="range-slider"
                gutterBottom
                sx={{
                    fontWeight: 'bold',
                    color: 'green',
                    mb: 2,
                    mt: 2,
                    fontSize: '1.8rem',
                    textAlign: 'center'
                }}
            >
                Khong gi
            </Typography>

            <Slider
                getAriaLabel={() => 'Price range'}
                value={priceRange}
                onChange={handleSliderChange}
                valueLabelDisplay="auto"
                min={0}
                max={999}
                getAriaValueText={(value) => `${value}`}
            />
            <Box sx={{ display: 'flex', justifyContent: 'space-between' }}>
                <Typography>{priceRange[0]}.000</Typography>
                <Typography>{priceRange[1]}.000</Typography>
            </Box>
        </Box>
    );
};


