const LeftColumn = ({ priceRange, onPriceChange }) => {
    // const categories = [];

    return (
        <div className="left-column">
            <PriceFilter
                priceRange={priceRange}
                onPriceChange={onPriceChange}
            />
            {/*<Categories categories={categories} />*/}
        </div>
    );
};

const RightColumn = ({ products, onSearch, searchKeyword, currentPage, totalPages, handlePageChange, onSort }) => (
    <div className="right-column">
        <div className="search-sort-container">
            <SearchBar onSearch={onSearch} />
            <SortOptions onSort={onSort} />
        </div>
        <ProductGrid products={products} searchKeyword={searchKeyword} />
        <Pagination
            currentPage={currentPage}
            totalPages={totalPages}
            onPageChange={handlePageChange}
        />
    </div>
);
const Pagination = ({ currentPage, totalPages, onPageChange }) => {
    const pageNumbers = [];

    for (let i = 1; i <= totalPages; i++) {
        pageNumbers.push(i);
    }

    const goToNextPage = () => {
        if (currentPage < totalPages) {
            onPageChange(currentPage + 1);
        }
    }

    const goToPrevPage = () => {
        if (currentPage > 1) {
            onPageChange(currentPage - 1);
        }
    }

    return (
        <ul className="pagination">
            <li>
                <button
                    className="page-link"
                    onClick={goToPrevPage}
                    disabled={currentPage === 1}
                >
                    Trang trc
                </button>
            </li>
            {pageNumbers.map((pageNumber) => (
                <li
                    key={pageNumber}
                    className={`page-item ${currentPage === pageNumber ? 'active' : ''}`}
                >
                    <button
                        className="page-link"
                        onClick={() => onPageChange(pageNumber)}
                    >
                        {pageNumber}
                    </button>
                </li>
            ))}
            <li>
                <button
                    className="page-link"
                    onClick={goToNextPage}
                    disabled={currentPage === totalPages}
                >
                    Trang sau
                </button>
            </li>
        </ul>
    );
};


const Shop = () => {
    const [products, setProducts] = useState([]);
    const [searchKeyword, setSearchKeyword] = useState('');
    const [sortOption, setSortOption] = useState(null);
    const [currentPage, setCurrentPage] = useState(1);
    const [pageSize, setPageSize] = useState(12);
    const [totalPages, setTotalPages] = useState(0);

    const [minPrice, setMinPrice] = useState(0);
    const [maxPrice, setMaxPrice] = useState(1000000);


    const handleMinPriceChange = (e) => {
        const value = parseInt(e.target.value);
        setMinPrice(value);
    };

    const handleMaxPriceChange = (e) => {
        const value = parseInt(e.target.value);
        setMaxPrice(value);
    };

    const [priceRange, setPriceRange] = useState([0, 1000]);

    const handlePriceChange = (newRange) => {
        setPriceRange(newRange);
    };

    const handleSelectGenre = (genreId) => {
        axios.get(`http://fall2324w3g8.int3306.freeddns.org/api/book/genre/${genreId}?page=${currentPage}&size=${pageSize}`)
            .then(response => {
                if (response.data && Array.isArray(response.data.content)) {
                    setProducts(response.data.content);
                    setTotalPages(response.data.totalPages);
                } else {
                    console.error('Nhn d liu khng hp l');
                }
            })
            .catch(error => {
                console.error('Li khi ly d liu sch theo th loi: ', error);
            });
    };

    useEffect(() => {
        axios.get(`http://fall2324w3g8.int3306.freeddns.org/api/book/all?page=${currentPage}&size=${pageSize}`)
            .then(response => {
                if (response.data && Array.isArray(response.data.content)) {
                    setProducts(response.data.content);
                    setTotalPages(response.data.totalPages);
                } else {
                    console.error('Nhn d liu khng hp l');
                }
            })
            .catch(error => {
                console.error('Li khi ly d liu bng axios: ', error);
            });
    }, [currentPage, pageSize]);

    const handleSearch = (keyword) => {
        setSearchKeyword(keyword);
    };

    const handleSort = (option) => {
        setSortOption(option);
    };

    const handlePageChange = (newPage) => {
        setCurrentPage(newPage);
        window.scrollTo(0, 0);
    };


    const sortedProducts = () => {
        if (!Array.isArray(products)) {
            return [];
        }

        let sorted = [...products];

        if (sortOption === 'price_asc') {
            sorted.sort((a, b) => a.price - b.price);
        } else if (sortOption === 'price_desc') {
            sorted.sort((a, b) => b.price - a.price);
        }

        return sorted;
    };

    return (
        <div className="shop-container">
            <LeftColumn
                priceRange={priceRange}
                onPriceChange={handlePriceChange}
            />
            <RightColumn
                onSort={handleSort}
                products={sortedProducts()}
                onSearch={handleSearch}
                searchKeyword={searchKeyword}
                currentPage={currentPage}
                totalPages={totalPages}
                handlePageChange={handlePageChange}
            />
            {/*<Categories onSelectGenre={handleSelectGenre} />*/}
        </div>
    );
};

export default Shop;
