import PropTypes from "prop-types";
import React, {Fragment, useEffect, useState} from "react";
import "./styleCheckout.css";
import axios from "axios";
import Modal from 'react-bootstrap/Modal';
import Button from 'react-bootstrap/Button';
import 'react-toastify/dist/ReactToastify.css';
import {ToastContainer,toast} from 'react-toastify'
import { useNavigate } from "react-router-dom";
import {getCart, getBookInfo, changeCart} from "../../apiServices/CartService";
import {getUserInfo} from "../../apiServices/CheckoutService"
const Checkout = () => {
    const [cartItems, setCartItems] = useState([]);
    const [totalPrice, setTotalPrice] = useState(0);
    const [userInfo, setUserInfo] = useState({
        fullName: '',
        phoneNumber: '',
        address: '',
        email: ''
    });

    const { fullName, address, phoneNumber, email } = userInfo;
    const bookId = 1;
    const quantity = 1;
    const accessToken = localStorage.getItem('accessToken');
    const orderData = {
        deliveryAddress: address,

        bookQuantities: [

            {
                bookId: bookId,
                quantity: quantity
            }
        ]
    };
    const [show, setShow] = useState(false);

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);
    const navigate = useNavigate();

    const returnShop = () => {
        navigate("/shop");

    }

    const createOrder = () =>
    {
        if (cartItems.length === 0) {
            toast.error("Gi hng ca bn ang trng");
            return;
        }
        const bookQuantities = cartItems.map(item => ({
            bookId: item.id,
            quantity: item.quantity,

        }
        ));
        cartItems.forEach(item => {
            changeCart(item.id, 0).then(response => {

            }).catch((error) => {
                console.log(error)
            });

        });
        // Update orderData with the dynamically generated bookQuantities
        const updatedOrderData = {
            ...orderData,
            bookQuantities: bookQuantities,
        };
        axios.post('http://fall2324w3g8.int3306.freeddns.org/api/user/orm/orders/create', updatedOrderData, {headers : {'Authorization': 'Bearer '+ accessToken}}).then(()=>{
            handleShow();


            toast.success("t hng thnh cng");
        }).catch((error) => {
            console.error("Error creating order:", error);
        });
    }
    const cartDetail = () => {
        getCart()
            .then((response) => {
                const bookData = response.data.bookQuantities;
                const promises = bookData.map((book) =>
                    getBookInfo(book.bookId)
                        .then((responseBook) => {
                            const bookInfo = {
                                id: book.bookId,
                                title: responseBook.data.title,
                                price: responseBook.data.price,
                                imgUrl: responseBook.data.imgUrl.replace('public/', ''),
                                quantity: book.quantity,
                            };

                            return bookInfo;
                        })
                        .catch((error) => {
                            console.log(error);
                        })
                );

                Promise.all(promises)
                    .then((bookInfoArray) => {
                        const totalPrice = bookInfoArray.reduce((total, item) => total + item.price * item.quantity, 0);

                        setCartItems(bookInfoArray);
                        setTotalPrice(totalPrice);
                    })
                    .catch((error) => {
                        console.log(error);
                    });
            })
            .catch((error) => {
                console.log(error);
            });
    };

    const updateUserInfo = (user) => {
        setUserInfo({
            fullName: user.fullName,
            address: user.address,
            phoneNumber: user.phoneNumber,
            email: user.email
        });
    };
    const getInfo = () => {
        getUserInfo().then(response => {
            console.log(response.data);
            updateUserInfo(response.data);
        }).catch(error => {
            console.error("Error getting user info:", error);
        });
    }

    useEffect(() => {
        cartDetail();
        getInfo();
    }, []);
    return (
        <Fragment>


            <div className="checkout-area pt-95 pb-100">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-7">
                            <form className="billing-info-wrap rounded shadow p-5">
                                <h3>Thng tin t hng</h3>

                                <div className="billing-info mb-20 form-group">
                                    <label>Tn y </label>
                                    <input type="text" className="form-control" value={fullName}/>
                                </div>



                                <div className="billing-info mb-20 form-group">
                                    <label>a ch</label>
                                    <input type="text" className="form-control" value={address}/>
                                </div>

                                <div className="billing-info mb-20 form-group">
                                    <label>S in thoi</label>
                                    <input type="text" className="form-control" value={phoneNumber}/>
                                </div>

                                <div className="billing-info mb-20 form-group">
                                    <label>Email</label>
                                    <input type="text" className="form-control" value={email}/>
                                </div>

                                <div>
                                    <h4>Thng tin b sung</h4>
                                    <div className="additional-info form-group">
                                        <label>Ghi ch</label>
                                        <textarea
                                            className="form-control"
                                            placeholder="Ghi ch cho ca hng v n v vn chuyn "
                                            name="message"
                                            defaultValue={""}
                                        />
                                    </div>
                                </div>
                            </form>


                        </div>

                        <div className="col-lg-5">
                            <div className="your-order-area">
                                <h3> n hng ca bn </h3>
                                <div className="your-order-wrap gray-bg-4">
                                    <div className="your-order-product-info">
                                        <div className="your-order-top">
                                            <ul>
                                                <li>Sn phm</li>
                                                <li>n gi</li>
                                            </ul>
                                        </div>
                                        <div className="your-order-middle">
                                            {cartItems.map((item, index) => (

                                                <ul>

                                                <li>

                                  <span className="order-middle-left">
                                    {item.title} X {item.quantity}
                                  </span>{" "}
                                                    <span className="order-price">
                                                        {item.quantity*item.price}
                                  </span>
                                                </li>

                                            </ul>
                                            ))}

                                        </div>
                                        <div className="your-order-bottom">
                                            <ul>
                                                <li className="your-order-shipping">Ph vn chuyn</li>
                                                <li>Min ph</li>
                                            </ul>
                                        </div>
                                        <div className="your-order-total">
                                            <ul>
                                                <li className="order-total">Tng</li>
                                                <li>
                                                    {totalPrice}
                                                </li>
                                            </ul>
                                        </div>
                                    </div>
                                    <div className="payment-method"></div>
                                </div>
                                <div className="place-order mt-25">
                                    <button className="btn-hover" onClick={createOrder}>t hng</button>
                                </div>

                            </div>
                        </div>
                    </div>

                </div>
            </div>
            <Modal
                show={show}
                onHide={handleClose}
                centered
                backdrop="static"
                keyboard={false}
            >
                <Modal.Header className="d-block modal-header-custom">
                    <Modal.Title>t hng thnh cng!</Modal.Title>
                </Modal.Header>
                <Modal.Body className="text-center">Cm n bn  tin tng s dng dch v v sn phm ca chng ti</Modal.Body>
                <Modal.Footer className="justify-content-center">
                    <button className="cart-btn-2" onClick={returnShop}>Tr v</button>
                </Modal.Footer>
            </Modal>
            <ToastContainer />
        </Fragment>

    )
}

export default Checkout