import React, { useState } from "react";
import { Form, Button } from "react-bootstrap";
import { changePassword } from "../../apiServices/ChangePasswordService";
import { toast, ToastContainer } from "react-toastify";
import { useNavigate } from "react-router-dom";

function ChangePassword() {
  const [oldPassword, setOldPassword] = useState("");
  const [newPassword, setNewPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [error, setError] = useState("");
  const navigate = useNavigate()

  const handlePasswordChange = async () => {
    if (newPassword !== confirmPassword) {
      setError("Mt khu xc nhn khng trng khp. Vui lng nhp li.");
    } else if (!isPasswordValid(newPassword)) {
      setError("Mt khu khng p ng yu cu an ton. Vui lng th li.");
    } else {
      await changePassword({ oldPassword, newPassword })
        .then((response) => {
          // X l kt qu t API nu cp nht thnh cng
          localStorage.clear();
          navigate("/login")
          toast("i mt khu thnh cng. Vui lng ng nhp li!");
        })
        .catch((error) => {
          // X l li t API nu c li
          console.error(error);
          setError("Mt khu c khng ng. Vui lng nhp li");
        });
    }
  };

  const isPasswordValid = (password) => {
    // S dng biu thc chnh quy (regex)  kim tra mt khu
    const passwordRegex =
      /^(?=.*[A-Z])(?=.*[a-z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,}$/;
    return passwordRegex.test(password);
  };
  return (
    <div
      className="d-flex justify-content-center"
      style={{ alignItems: "top" }}
    >
      <Form style={{ width: "30%" }}>
        <h1>i Mt Khu</h1>
        <Form.Floating id="oldPassword" className="mb-3">
          <Form.Control
            id="old"
            type="password"
            placeholder="Nhp mt khu c"
            value={oldPassword}
            onChange={(e) => setOldPassword(e.target.value)}
          />
          <label htmlFor="old">Nhp mt khu c</label>
        </Form.Floating>

        <Form.Floating id="newPassword" className="mb-3">
          <Form.Control
            id="new"
            type="password"
            placeholder="Nhp mt khu mi"
            value={newPassword}
            onChange={(e) => setNewPassword(e.target.value)}
          />
          <label htmlFor="new">Nhp mt khu mi</label>
        </Form.Floating>

        <Form.Floating id="confirmPassword" className="mb-3">
          <Form.Control
            id="confirm"
            type="password"
            placeholder="Xc nhn mt khu mi"
            value={confirmPassword}
            onChange={(e) => setConfirmPassword(e.target.value)}
          />
          <label htmlFor="confirm">Xc nhn mt khu mi</label>
        </Form.Floating>
        {error && <p className="text-danger">{error}</p>}

        <Button
          variant="primary"
          onClick={handlePasswordChange}
          className="mb-3"
        >
          i Mt Khu
        </Button>
      </Form>
      <ToastContainer />
    </div>
  );
}

export default ChangePassword;
