import React, { useState } from "react";
import {
  MDBBtn,
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBCard,
  MDBCardBody,
  MDBInput,
} from "mdb-react-ui-kit";

import "./styleLogin.css";
import { Button } from "react-bootstrap";

import axios from "axios";
import { Link, useNavigate } from "react-router-dom";
import { jwtDecode } from "jwt-decode";

function Login() {
  const navigate = useNavigate();
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [loggedIn, setLoggedIn] = useState(false);
  const [loginError, setLoginError] = useState("");
  const [alertLogin, setAlertLogin] = useState(null);

  const handleUsernameChange = (event) => {
    setUsername(event.target.value);
  };

  const handlePasswordChange = (event) => {
    setPassword(event.target.value);
  };

  const handleLogin = () => {
    if (!username || !password) {
      setLoginError("Tn ng nhp hoc mt khu khng hp l!");
    } else {
      axios
        .post("http://fall2324w3g8.int3306.freeddns.org/api/auth/authenticate", {
          username,
          password,
        })
        .then((response) => {
          if (response.data.accessToken) {
            const accessToken = response.data.accessToken;

            localStorage.setItem("accessToken", accessToken);

            const storedAccessToken = localStorage.getItem("accessToken");
            if (storedAccessToken) {
              if (storedAccessToken === response.data.accessToken) {
                if (jwtDecode(storedAccessToken).role == "ROLE_USER") {
                  window.location.href = "/";
                } else if (jwtDecode(storedAccessToken).role == "ROLE_ADMIN") {
                  window.location.href = "/products";
                }
              }
            }
          }
        })
        .catch((error) => {
          console.log(error);
          setLoginError("Tn ng nhp hoc mt khu khng hp l!");
        });
    }
  };
  return (
    <MDBContainer
      fluid
      className="p-4 background-radial-gradient overflow-hidden"
    >
      <MDBRow>
        <MDBCol
          md="6"
          className="text-center text-md-start d-flex flex-column justify-content-center"
        >
          <h1
            className="my-5 display-3 fw-bold ls-tight px-3"
            style={{ color: "hsl(218, 81%, 95%)" }}
          >
            BookStore <br />
            <span style={{ color: "hsl(218, 81%, 75%)" }}>
              Th tm hn vo sch
            </span>
          </h1>

          <p className="px-3" style={{ color: "hsl(218, 81%, 85%)" }}>
            n vi ca hng ca chng ti, bn khng ch n vi ni bn nhng
            cun sch cht lng v uy tn, hy cng cm nhn v ho mnh vo l
            tng ca tc gi trong nhng cun sch ni ting, hay v tm c
            nht ca ca hng chng ti.
          </p>
        </MDBCol>

        <MDBCol md="6" className="position-relative">
          <div
            id="radius-shape-1"
            className="position-absolute rounded-circle shadow-5-strong"
          ></div>
          <div
            id="radius-shape-2"
            className="position-absolute shadow-5-strong"
          ></div>

          <MDBCard className="my-5 bg-glass">
            <MDBCardBody className="p-5">
              <MDBInput
                wrapperClass="mb-3"
                id="form3"
                type="text"
                placeholder="Tn ti khon"
                value={username}
                onChange={handleUsernameChange}
              />

              <MDBInput
                wrapperClass="mb-3"
                id="form4"
                type="password"
                placeholder="Mt khu"
                value={password}
                onChange={handlePasswordChange}
              />
              {loginError && <p className="text-danger">{loginError}</p>}
              <Button className="w-100 mb-3" size="md" onClick={handleLogin}>
                ng nhp
              </Button>

              <div className="text-center">
                <p>Bn cha c ti khon?</p>
                <Link to="/register">ng k</Link>
              </div>
            </MDBCardBody>
          </MDBCard>
        </MDBCol>
      </MDBRow>
    </MDBContainer>
  );
}

export default Login;
