import React from "react";
import { useNavigate, NavLink } from "react-router-dom";

import Container from "react-bootstrap/Container";
import Nav from "react-bootstrap/Nav";
import Navbar from "react-bootstrap/Navbar";
import NavDropdown from "react-bootstrap/NavDropdown";

import "@fortawesome/fontawesome-free/css/all.min.css";

import logo from "../../logo.png";
import { jwtDecode } from "jwt-decode";
import { toast, ToastContainer } from "react-toastify";

const Header = () => {
  const navigate = useNavigate();

  const logout = () => {
    localStorage.clear();
    window.location.href = "/";
  };

  const handleCart = () => {
    if (localStorage.getItem("accessToken")) {
      navigate("/cart");
    } else {
      const currentURL = "/cart";
      localStorage.setItem("currentURL", currentURL);
      navigate("/login");
      toast("Vui lng ng nhp  xem gi hng");
    }
  };

  return (
    <Navbar expand="lg" className="bg-body-tertiary">
      <Container>
        <Navbar.Brand
          onClick={() => navigate("/")}
          style={{ marginRight: "20%" }}
        >
          <img
            src={logo}
            alt="Logo book shop"
            style={{ width: "100px", borderRadius: "50%", cursor: "pointer" }}
          ></img>
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="me-auto">
            <Nav.Link as={NavLink} to="/">
              Trang ch
            </Nav.Link>
            <Nav.Link as={NavLink} to="/shop">
              Ca hng
            </Nav.Link>
            <Nav.Link as={NavLink} to="/about">
              V chng ti
            </Nav.Link>
            <Nav.Link onClick={handleCart}>
              <i className="fa-solid fa-cart-shopping"></i>
            </Nav.Link>
            {localStorage.getItem("accessToken") ? (
              <NavDropdown
                title={
                  <span>
                    <i className="fa-solid fa-user"></i>
                    {jwtDecode(localStorage.getItem("accessToken")).fullName}
                  </span>
                }
                id="basic-nav-dropdown"
              >
                <NavDropdown.Item onClick={() => navigate("/user/account")}>
                  Qun l ti khon
                </NavDropdown.Item>
                <NavDropdown.Item onClick={() => navigate("/user/order")}>
                  n hng ca ti
                </NavDropdown.Item>
                <NavDropdown.Item
                  onClick={() => navigate("/user/changePassword")}
                >
                  i mt khu
                </NavDropdown.Item>
                <NavDropdown.Divider />
                <NavDropdown.Item onClick={logout}>ng xut</NavDropdown.Item>
              </NavDropdown>
            ) : (
              <>
                <Nav.Link as={NavLink} to={"/register"}>
                  ng k
                </Nav.Link>
                <Nav.Link as={NavLink} to={"/login"}>
                  ng nhp
                </Nav.Link>
              </>
            )}
          </Nav>
        </Navbar.Collapse>
      </Container>
      <ToastContainer />
    </Navbar>
  );
};

export default Header;
