import React from "react";
import { NavLink, useNavigate } from "react-router-dom";
import { Nav } from "react-bootstrap";
import "./Sidebar.css";
const Sidebar = () => {
  const handleLogout = () => {
    window.location.href = "/login";
    localStorage.clear();
  }
  return (
    <div className="sidebar-ad">
      <Nav
        className="flex-column"
      >
        <Nav.Item>
          <Nav.Link as={NavLink} to="/products">Qun l sn phm</Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link as={NavLink} to="/orders">Qun l n hng</Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link as={NavLink} to="/statistics">Thng k</Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link onClick={handleLogout}>ng xut</Nav.Link>
        </Nav.Item>
      </Nav>
    </div>
  );
};

export default Sidebar;
